.go-to-top-button {
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 4;
}
.my-button {
    background-color: #f0f0f0; /* Light gray background */
    color: #333333; /* Darker text color for better readability */
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease, color 0.3s ease;
}

.my-button:hover {
    background-color: #dcdcdc; /* Slightly darker gray on hover */
}

.go-to-top-button.visible {
    opacity: 1;
}
.arr{
    background-color: #333333; /* Light gray background */
    color:#f0f0f0 ; /* Darker text color for better readability */
    border: none;
    font-size: 2rem;
    /*padding: 8px 15px;*/
    border-radius: 100%;
    cursor: pointer;
}
.arr:hover{
    color: #dcdcdc; /* Slightly darker gray on hover */

}
@media screen and (min-width: 431px) {
    /* For screens larger than 430px */
    .go-to-top-button {
        top: 20px;
    }
}

@media screen and (max-width: 430px) {
    /* For screens smaller than or equal to 430px */
    .go-to-top-button {
        bottom: 20px;
        left: 90%;
    }
}
