.dropdown-menu {
    display: none;
    font-size: 1rem;
    position: absolute;
    background-color: #26272b;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

.dropdown-menu a {
    float: none;
    color: white;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
}


/* Media queries for adjusting styles based on screen width */

@media screen and (max-width: 990px) {


    .navbar {
        display: flex ;
        justify-content: space-between;
        width: auto;
    }
    .navbar-links {
        display: none;
    }

    .search-container {
        /*margin-top: 10px;*/
    }

    .dropdown{
        float: left;
        overflow: hidden;
        /*margin-right: 1rem;*/
        display: inline-block;
    }
    .dropdown-menu a:hover {
        background-color: grey;
    }

    .dropdown:hover .dropdown-menu {
        display:block;
        border: 1px solid white;
    }

}

@media screen and (max-width: 680px) {
    .navbar-logo h1 {
        /*margin-bottom: 10px;*/
        font-size: 1.8rem;
        margin-bottom: 0.3rem;
    }
    h1 {
        /*margin-bottom: 10px;*/
        font-size: 1.5rem;
    }
    .navbar-logo{
        padding: 1rem;
    }
    .navbar {
        display: flex ;
        flex-direction: column;
        width: auto;
    }
    .navbar-links {
        display: none;
    }

    .search-container {
        /*margin-top: 10px;*/
    }

    .dropdown-mobile {
        display: flex;
        /*flex-direction: column;*/
        position: relative;
        flex-wrap: wrap;
        justify-content: space-evenly;
        align-items: center;
        /*top: 50px;*/
        left: 0;
        background-color: #26272b;
        padding: 10px;
        z-index: 1;
        border: solid 1px white ;
        /*max-width:max-content;*/
        /*display: block;*/
    }

    .dropdown-mobile a {
        color: #fff;
        text-decoration: none;
        font-size: 16px;
        margin-bottom: 5px;
        padding: 0.5rem;

    }
    .dropdown-mobile a:hover{

        background-color: grey;
    }

    .dropdown:hover .dropdown-menu {
        display: none;

    }
}
@media screen and (max-width: 430px) {
    .navbar-logo h1 {
        /*margin-bottom: 10px;*/
        font-size: 1.5rem;
        margin-bottom: 0.3rem;
    }
    h1 {
        /*margin-bottom: 10px;*/
        font-size: 1.4rem;
    }

    .icon {
        width: 3rem;
    }
}
